<template>
  <div id="index">
    <div id="alert_area" style="position:fixed;z-index: 10;top: 0;width: 100%;">
      <b-alert
        :show="dismissCountDown"
        dismissible
        fade
        variant="warning"
        @dismiss-count-down="countDownChanged"
      >
        <strong>{{message}}</strong>
        （本次提示将在{{ dismissCountDown }}秒后消失...）
      </b-alert>
      <!-- <div class="alert alert-warning alert-dismissible fade show" v-if="message" role="alert">
        <strong>{{ message }}</strong>
        <button type="button" class="close" v-on:click="hideMsg()" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>-->
    </div>
    <div class="container-fluid">
      <header></header>
      <!-- header end -->
      <div class="main">
        <div class="content">
          <div class="mb-3 mb-xl-5"></div>

          <b-overlay :show="showOverlay" class="d-inline-block">
            <div class="card mb-3 mb-xl-5">
              <div class="card-header d-md-flex align-items-center">
                <h5 class="h6 text-uppercase font-weight-semi-bold mb-2 mb-md-0">系统服务</h5>
              </div>
              <div class="card-body pt-0">
                <div class="row no-edge-distance">
                  <div
                    class="col-12 col-md-4 no-edge-distance"
                    v-for="s in services"
                    :key="s.osServerName"
                  >
                    <div class="row no-edge-distance">
                      <div class="col-8 service-name">
                        <div class="m-md-2">
                          {{ s.osServerName }}
                          <b-icon-arrow-down variant="danger" v-if="!s.osServerStatus"></b-icon-arrow-down>
                          <b-icon-arrow-up variant="success" v-if="s.osServerStatus"></b-icon-arrow-up>
                        </div>
                      </div>
                      <div class="col-4 no-edge-distance">
                        <b-dropdown
                          id="btnGroupDrop1"
                          text="操作"
                          class="m-md-2"
                          variant="link btn-sm dropdown-toggle"
                        >
                          <b-dropdown-item
                            v-for="o in options"
                            :key="o.key"
                            v-on:click="doOp(o.key,s.osServerEnName)"
                          >{{ o.value }}</b-dropdown-item>
                        </b-dropdown>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </b-overlay>
        </div>
        <!-- content end -->
      </div>
      <!-- main end -->
      <footer></footer>
      <!-- footer end -->
    </div>
  </div>
</template>
<script>
export default {
  name: "App",
  data() {
    return {
      showOverlay: false,
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      options: [
        { key: 1, value: "关闭" },
        { key: 2, value: "启动" },
        { key: 3, value: "强制关闭" },
        { key: 4, value: "重启" }
      ],
      services: [],
      message: ""
    };
  },
  created: function() {
    this.showOverlay = true;
    this.$axios
      .get("./allservicestatus.json")
      .then(response => {
        setTimeout(() => {
          this.load();
        }, 3000);
        if (!response.data) {
          this.showAlert("获取数据失败");
        } else {
          if (response.data.code == 0) {
            //console.log(response.data.data);
            this.services = response.data.data;
            this.showOverlay = false;
          } else {
            this.showAlert("获取数据失败");
          }
        }
      })
      .catch(error => {
        this.showOverlay = false;
        this.showAlert("获取数据失败(" + error + ")");
        setTimeout(() => {
          this.load();
        }, 3000);
      });
  },
  methods: {
    load() {
      this.$axios
        .get("./allservicestatus.json")
        .then(response => {
          setTimeout(() => {
            this.load();
          }, 3000);
          if (!response.data) {
            this.showAlert("获取数据失败");
          } else {
            if (response.data.code == 0) {
              //console.log(response.data.data);
              this.services = response.data.data;
              this.showOverlay = false;
            } else {
              this.showAlert("获取数据失败");
            }
          }
        })
        .catch(error => {
          //this.showOverlay = false;
          setTimeout(() => {
            this.load();
          }, 3000);
          this.showAlert("获取数据失败(" + error + ")");
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert(message) {
      this.message = message;
      this.dismissCountDown = this.dismissSecs;
    },
    doOp: function(key, serviceName) {
      //this.hideMsg();
      //debugger;
      if (key == 1) {
        this.$bvModal
          .msgBoxConfirm("确定关闭服务?", {
            title: "请确认",
            size: "sm",
            buttonSize: "sm",
            okVariant: "danger",
            okTitle: "是的",
            cancelTitle: "点错了",
            footerClass: "p-2",
            hideHeaderClose: false,
            centered: true
          })
          .then(ok => {
            if (ok) {
              this.stopService(serviceName);
            }
          })
          .catch(err => {
            console.error(err);
          });
      }
      if (key == 2) {
        this.$bvModal
          .msgBoxConfirm("确定启动服务?", {
            title: "请确认",
            size: "sm",
            buttonSize: "sm",
            okVariant: "danger",
            okTitle: "是的",
            cancelTitle: "点错了",
            footerClass: "p-2",
            hideHeaderClose: false,
            centered: true
          })
          .then(ok => {
            if (ok) {
              this.startService(serviceName);
            }
          })
          .catch(err => {
            console.error(err);
          });
      }
      if (key == 3) {
        this.$bvModal
          .msgBoxConfirm("确定强制关闭服务?", {
            title: "请确认",
            size: "sm",
            buttonSize: "sm",
            okVariant: "danger",
            okTitle: "是的",
            cancelTitle: "点错了",
            footerClass: "p-2",
            hideHeaderClose: false,
            centered: true
          })
          .then(ok => {
            if (ok) {
              this.forceStopService(serviceName);
            }
          })
          .catch(err => {
            console.error(err);
          });
      }
      if (key == 4) {
        this.$bvModal
          .msgBoxConfirm("确定重启服务?", {
            title: "请确认",
            size: "sm",
            buttonSize: "sm",
            okVariant: "danger",
            okTitle: "是的",
            cancelTitle: "点错了",
            footerClass: "p-2",
            hideHeaderClose: false,
            centered: true
          })
          .then(ok => {
            if (ok) {
              this.restartService(serviceName);
            }
          })
          .catch(err => {
            console.error(err);
          });
      }
    },

    restartService: function(serviceName) {
      //重启服务
      this.showOverlay = true;
      let param = new FormData();
      param.append("serviceName", serviceName);
      this.$axios
        .post("./restart_service.json", param)
        .then(response => {
          this.showOverlay = false;
          if (!response.data) {
            this.showAlert("操作失败");
          } else {
            if (response.data.code == 0) {
              this.showAlert("操作成功");
            } else {
              this.showAlert("操作失败" + response.data.msg);
            }
          }
        })
        .catch(error => {
          this.showOverlay = false;
          this.showAlert("操作失败," + error);
        });
    },

    forceStopService: function(serviceName) {
      //重启服务
      this.showOverlay = true;
      let param = new FormData();
      param.append("serviceName", serviceName);
      this.$axios
        .post("./force_stop_service.json", param)
        .then(response => {
          this.showOverlay = false;
          if (!response.data) {
            this.showAlert("操作失败");
          } else {
            if (response.data.code == 0) {
              this.showAlert("操作成功");
            } else {
              this.showAlert("操作失败" + response.data.msg);
            }
          }
        })
        .catch(error => {
          this.showOverlay = false;
          this.showAlert("操作失败," + error);
        });
    },

    stopService: function(serviceName) {
      //重启服务
      this.showOverlay = true;
      let param = new FormData();
      param.append("serviceName", serviceName);
      this.$axios
        .post("./stop_service.json", param)
        .then(response => {
          this.showOverlay = false;
          if (!response.data) {
            this.showAlert("操作失败");
          } else {
            if (response.data.code == 0) {
              this.showAlert("操作成功");
            } else {
              this.showAlert("操作失败" + response.data.msg);
            }
          }
        })
        .catch(error => {
          this.showOverlay = false;
          this.showAlert("操作失败," + error);
        });
    },

    startService: function(serviceName) {
      //重启服务
      this.showOverlay = true;
      let param = new FormData();
      param.append("serviceName", serviceName);
      this.$axios
        .post("./start_service.json", param)
        .then(response => {
          this.showOverlay = false;
          if (!response.data) {
            this.showAlert("操作失败");
          } else {
            if (response.data.code == 0) {
              this.showAlert("操作成功");
            } else {
              this.showAlert("操作失败" + response.data.msg);
            }
          }
        })
        .catch(error => {
          this.showOverlay = false;
          this.showAlert("操作失败," + error);
        });
    }
  }
};
</script>

