<template>
  <div>
    <b-breadcrumb class="bg-transparent">
      <b-breadcrumb-item active>
        <b-icon icon="house-fill" scale="1.25" shift-v="1.25" aria-hidden="true"></b-icon>角色与权限配置
      </b-breadcrumb-item>
    </b-breadcrumb>
    <b-container fluid class="bv-example-row">
      <b-row>
        <b-col lg="6" class="my-1">
          <b-form-group
            label="排序"
            label-cols-sm="3"
            label-align-sm="right"
            label-size="sm"
            label-for="sortBySelect"
            class="mb-0"
          >
            <b-input-group size="sm">
              <b-form-select v-model="sortBy" id="sortBySelect" :options="sortOptions" class="w-75">
                <template v-slot:first>
                  <option value>-- none --</option>
                </template>
              </b-form-select>
              <b-form-select v-model="sortDesc" size="sm" :disabled="!sortBy" class="w-25">
                <option :value="false">正序</option>
                <option :value="true">倒序</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col lg="6" class="my-1">
          <b-form-group
            label="初始排序"
            label-cols-sm="3"
            label-align-sm="right"
            label-size="sm"
            label-for="initialSortSelect"
            class="mb-0"
          >
            <b-form-select
              v-model="sortDirection"
              id="initialSortSelect"
              size="sm"
              :options="[{ value: 'asc', text: '正序' },
              { value: 'desc', text: '倒序' },{ value: 'last', text: '上一次' }]"
            ></b-form-select>
          </b-form-group>
        </b-col>

        <b-col lg="6" class="my-1">
          <b-form-group
            label="过滤"
            label-cols-sm="3"
            label-align-sm="right"
            label-size="sm"
            label-for="filterInput"
            class="mb-0"
          >
            <b-input-group size="sm">
              <b-form-input v-model="filter" type="search" id="filterInput" placeholder="输入搜索"></b-form-input>
              <b-input-group-append>
                <b-button :disabled="!filter" @click="filter = ''">清除</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col lg="6" class="my-1">
          <b-form-group
            label="过滤条件"
            label-cols-sm="3"
            label-align-sm="right"
            label-size="sm"
            description="全部都不选以筛选所有数据"
            class="mb-0"
          >
            <b-form-checkbox-group v-model="filterOn" class="mt-1">
              <b-form-checkbox value="name">角色名称</b-form-checkbox>
              <b-form-checkbox value="description">角色描述</b-form-checkbox>
              <b-form-checkbox value="systemDefault">是否系统默认</b-form-checkbox>
            </b-form-checkbox-group>
          </b-form-group>
        </b-col>

        <b-col sm="4" md="4" class="my-1">
          <div>
            <b-button-toolbar aria-label="Toolbar with button groups and dropdown menu">
              <b-button-group class="mx-1">
                <b-button href="#/working_table/employee/add" variant="outline-secondary">新建</b-button>
                <b-button @click="editSelected" :disabled="!selected" variant="outline-secondary">编辑</b-button>
                <b-button
                  @click="deleteSelected"
                  :disabled="!selected"
                  variant="outline-secondary"
                >删除</b-button>
              </b-button-group>
              <!-- <b-dropdown class="mx-1" right text="menu">
                <b-dropdown-item>Item 1</b-dropdown-item>
                <b-dropdown-item>Item 2</b-dropdown-item>
                <b-dropdown-item>Item 3</b-dropdown-item>
              </b-dropdown>
              <b-button-group class="mx-1">
                <b-button>Save</b-button>
                <b-button>Cancel</b-button>
              </b-button-group>-->
            </b-button-toolbar>
          </div>
        </b-col>

        <b-col sm="3" md="4" class="my-1">
          <b-form-group
            label="每页条数"
            label-cols-sm="6"
            label-cols-md="4"
            label-cols-lg="3"
            label-align-sm="right"
            label-size="sm"
            label-for="perPageSelect"
            class="mb-0"
          >
            <b-form-select v-model="perPage" id="perPageSelect" size="sm" :options="pageOptions"></b-form-select>
          </b-form-group>
        </b-col>

        <b-col sm="5" md="4" class="my-1">
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            align="fill"
            size="sm"
            class="my-0"
          ></b-pagination>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12">
          <div>
            <!-- <p class="mt-3">Current Page: {{ currentPage }}</p> -->
            <div class="overflow-auto">
              <b-table
                id="my-table"
                ref="selectableTable"
                :items="myProvider"
                :busy.sync="isBusy"
                :fields="fields"
                :per-page="perPage"
                :current-page="currentPage"
                show-empty
                small
                bordered
                responsive
                selectable
                sticky-header="calc(100vh - 18rem)"
                select-mode="single"
                :filter="filter"
                :filterIncludedFields="filterOn"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :sort-direction="sortDirection"
                @filtered="onFiltered"
                @row-selected="onRowSelected"
                @row-dblclicked="onRowDblClicked"
              >
                <!-- <template v-slot:cell(name)="row">
                  {{ row.value.first }} {{ row.value.last }}
                </template>-->
                <template v-slot:cell(actions)="row">
                  <b-link @click="onRowDblClicked(row.item, row.index, $event.target)" href="#">查看详情</b-link>
                </template>
              </b-table>
            </div>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      perPage: 15,
      currentPage: 1,
      selected: null,
      totalRows: 1,
      pageOptions: [1, 5, 10, 15],
      sortBy: "",
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      filterOn: [],
      items: [],
      isBusy: false,
      fields: [
        {
          key: "name",
          label: "角色名称",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "description",
          label: "角色描述",
          sortable: true,
          class: "text-center"
        },
        {
          key: "systemDefault",
          label: "是否系统默认",
          formatter: value => {
            return value ? "是" : "否";
          },
          sortable: true,
          sortByFormatted: true,
          filterByFormatted: true
        },
        {
          key: "builtIn",
          label: "是否系统内建",
          formatter: value => {
            return value ? "是" : "否";
          },
          sortable: true,
          sortByFormatted: true,
          filterByFormatted: true
        },
        { key: "actions", label: "Actions" }
      ]
    };
  },
  computed: {
    rows() {
      return this.items.length;
    },
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key };
        });
    }
  },
  mounted() {
    // this.$axios
    //   .get("./roles", { params: { page: this.currentPage-1, size: this.perPage } })
    //   .then(response => {
    //     //debugger
    //     this.items = response.data._embedded.roles;
    //     this.totalRows = response.data.page.totalElements;
    //   })
    //   .catch(function(error) {
    //     console.log(error);
    //   });
  },
  methods: {
    myProvider(ctx) {
      let filterOn = this.filterOn;
      //debugger;
      var params = {
        page: ctx.currentPage - 1,
        size: ctx.perPage,
        sort: ctx.sortBy + "," + (ctx.sortDesc ? "desc" : "")
      };

      filterOn.forEach((value) => {
        params[value] = ctx.filter;
      });

      let promise = this.$axios.get("./roles/search/findRoles", {
        params: params
      });

      return promise
        .then(response => {
          const items = response.data._embedded.roles;
          this.totalRows = response.data.page.totalElements;
          this.isBusy = false;
          return items;
        })
        .catch(error => {
          this.isBusy = false;
          console.error(error)
          return [];
        });
    },
    onRowSelected(items) {
      //this.$router.replace('/working_table/employee/view/'+items[0].id);
      this.selected = items[0];
    },
    onRowDblClicked(item) {
      this.$router.replace("/working_table/employee/view/" + item.id);
      //this.selected = items[0]
    },
    editSelected() {
      if (!this.selected) return;
      this.$router.replace("/working_table/employee/edit/" + this.selected.id);
    },
    deleteSelected() {
      if (!this.selected) return;
      this.$bvModal
        .msgBoxConfirm("Please confirm that you want to delete this item.", {
          title: "Please Confirm",
          size: "sm",
          buttonSize: "sm",
          okVariant: "danger",
          okTitle: "YES",
          cancelTitle: "NO",
          footerClass: "p-2",
          hideHeaderClose: false,
          centered: true
        })
        .then(value => {
          console.info(value);
        })
        .catch(err => {
          console.error(err);
          // An error occurred
        });
    },
    selectAllRows() {
      this.$refs.selectableTable.selectAllRows();
    },
    clearSelected() {
      this.$refs.selectableTable.clearSelected();
    },
    selectThirdRow() {
      // Rows are indexed from 0, so the third row is index 2
      this.$refs.selectableTable.selectRow(2);
    },
    unselectThirdRow() {
      // Rows are indexed from 0, so the third row is index 2
      this.$refs.selectableTable.unselectRow(2);
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  }
};
</script>

