<template>
  <div class="container-fluid">
    <b-form class="form-signin" @submit="onSubmit" @reset="onReset">
      <h2 class="form-signin-heading">请登录</h2>
      <!-- use param.error assuming FormLoginConfigurer#failureUrl contains the query parameter error -->
      <!-- <th:block th:if="${param.error != null}">
            <div class="alert alert-danger" role="alert">
              <th:block th:if="${session.SPRING_SECURITY_LAST_EXCEPTION != null}">
                  <th:block th:text="${session.SPRING_SECURITY_LAST_EXCEPTION.message}" />
                </th:block>
            </div>
      </th:block>-->
      <!-- the configured LogoutConfigurer#logoutSuccessUrl is /login?logout and contains the query param logout -->
      <!-- <th:block th:if="${param.logout != null}">
            <div>
                您已登出。
            </div>
      </th:block>-->
      <p>
        <label for="username" class="sr-only">用户名</label>
        <input
          type="text"
          id="username"
          name="username"
          class="form-control"
          placeholder="用户名"
          required
          autofocus
          v-model="username"
        />
      </p>
      <p>
        <label for="password" class="sr-only">密 码</label>
        <input
          type="password"
          id="password"
          name="password"
          class="form-control"
          placeholder="密码"
          required
          v-model="password"
        />
      </p>
      <button class="btn btn-lg btn-primary btn-block">登录</button>
    </b-form>
    <!-- <b-card class="mt-3" header="Form Data Result">
      <pre class="m-0">{{ form }}</pre>
    </b-card>-->
    <!-- <footer class="navbar-fixed-bottom">
      <div class="container">
        <p style="font-weight: bold;">
          当前版本号:V
          <font th:text="${version}"></font>
        </p>
      </div>
    </footer>-->
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      username: "",
      password: "",
      message: ""
    };
  },
  created: function() {},
  methods: {
    onReset(evt) {
      evt.preventDefault();
    },
    onSubmit(evt) {
      evt.preventDefault();
      let param = new FormData();
      param.append("username", this.username);
      param.append("password", this.password);
      this.$axios
        .post("./login", param)
        .then(response => {
          if (!response.data) {
            alert("登录失败")
          } else {
            if (response.data.code == 0) {
              window.location.hash = "/";
            } else {
              alert("登录失败，" + response.data.msg);
            }
          }
        })
        .catch(error => {
          alert("登录失败，" + error);
        });
    }
  }
};
</script>

<style scoped>
.form-signin {
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.form-signin .form-signin-heading,
.form-signin .checkbox {
  margin-bottom: 10px;
}
