import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Login from '@/components/Login'
import WorkingTable from '@/components/WorkingTable'
import EmployeeList from '@/components/working_table/employee/List'
import EmployeeAdd from '@/components/working_table/employee/Add'
import EmployeeView from '@/components/working_table/employee/View'
import EmployeeEdit from '@/components/working_table/employee/Edit'

import RoleList from '@/components/working_table/role/List'
import RoleAdd from '@/components/working_table/role/Add'
import RoleView from '@/components/working_table/role/View'
import RoleEdit from '@/components/working_table/role/Edit'

import About from '@/components/About'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/working_table',
      // name: 'HelloWorld',
      component: WorkingTable,
      children: [
        {
          path: 'employee',
          name: 'EmployeeList',
          component: EmployeeList
        },
        {
          path: 'employee/add',
          name: 'EmployeeAdd',
          component: EmployeeAdd
        },
        {
          path: 'employee/view/:Id',
          name: 'EmployeeView',
          component: EmployeeView
        },
        {
          path: 'employee/edit/:Id',
          name: 'EmployeeEdit',
          component: EmployeeEdit
        },
        {
          path: 'role',
          name: 'RoleList',
          component: RoleList
        },
        {
          path: 'role/add',
          name: 'RoleAdd',
          component: RoleAdd
        },
        {
          path: 'role/view/:Id',
          name: 'RoleView',
          component: RoleView
        },
        {
          path: 'role/edit/:Id',
          name: 'RoleEdit',
          component: RoleEdit
        }
        
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/about',
      name: 'About',
      component: About
    }
  ]
})
